/**
 * https://github.com/home-assistant/frontend/blob/dev/src/common/entity/state_color.ts
 * This file is modified to allow for light entities to be included in the state color calculation
 * so that the multi-light background feature can work
 */

import { batteryStateColorProperty } from '@hass/common/entity/color/battery_color';
import { computeDomain } from '@hass/common/entity/compute_domain';
import { stateActive } from '@hass/common/entity/state_active';
import { slugify } from '@hass/common/string/slugify';
import { UNAVAILABLE } from '@hass/data/entity';
import { computeGroupDomain, type GroupEntity } from '@hass/data/group';
import { computeCssVariable } from '@hass/resources/css-variables';
import type { HassEntity } from '@hass/ws/types';

const STATE_COLORED_DOMAIN = new Set([
  'alarm_control_panel',
  'alert',
  'automation',
  'binary_sensor',
  'calendar',
  'camera',
  'climate',
  'cover',
  'device_tracker',
  'fan',
  'group',
  'humidifier',
  'input_boolean',
  'lawn_mower',
  'light',
  'lock',
  'media_player',
  'person',
  'plant',
  'remote',
  'schedule',
  'script',
  'siren',
  'sun',
  'switch',
  'timer',
  'update',
  'vacuum',
  'valve',
  'water_heater',
]);

// slightly modified from frontend/src/common/entity/state_color.ts
export const stateColorCss = (
  stateObj: HassEntity,
  scope: string,
  active?: boolean,
  state?: string,
) => {
  const compareState = state ?? stateObj?.state;
  if (compareState === UNAVAILABLE) {
    return `var(--state-unavailable-color)`;
  }

  const properties = stateColorProperties(stateObj, scope, active, state);
  if (properties) {
    return computeCssVariable(properties);
  }

  return undefined;
};

/**
 * This method has been modified slightly to account for other themes.
 */
export const domainStateColorProperties = (
  domain: string,
  stateObj: HassEntity,
  scope: string,
  active?: boolean,
  state?: string,
): string[] => {
  const compareState = state ?? stateObj.state;
  // active is only passed in for the main room entity
  // so if it's not passed in, we use the stateActive function to determine if the entity is active
  // we don't care about the theme in this context
  const isActive = active ?? stateActive(stateObj, state);

  // allow for theme override
  const properties: string[] = [`--state-color-${scope}-theme`];

  const stateKey = slugify(compareState, '_');
  const activeKey = isActive ? 'active' : 'inactive';

  const dc = stateObj.attributes.device_class;

  if (dc) {
    properties.push(`--state-${domain}-${dc}-${stateKey}-color`);
  }

  properties.push(
    `--state-${domain}-${stateKey}-color`,
    `--state-${domain}-${activeKey}-color`,
    `--state-${activeKey}-color`,
  );

  return properties;
};

// slightly modified from frontend/src/common/entity/state_color.ts
export const stateColorProperties = (
  stateObj: HassEntity,
  scope: string,
  active?: boolean,
  state?: string,
): string[] | undefined => {
  const compareState = state ?? stateObj?.state;
  const domain = computeDomain(stateObj.entity_id);
  const dc = stateObj.attributes.device_class;

  // Special rules for battery coloring
  if (domain === 'sensor' && dc === 'battery') {
    const property = batteryStateColorProperty(compareState);
    if (property) {
      return [`--state-color-${scope}-theme`, property];
    }
  }

  // Special rules for group coloring
  if (domain === 'group') {
    const groupDomain = computeGroupDomain(stateObj as GroupEntity);
    if (groupDomain && STATE_COLORED_DOMAIN.has(groupDomain)) {
      return domainStateColorProperties(
        groupDomain,
        stateObj,
        scope,
        active,
        state,
      );
    }
  }

  if (STATE_COLORED_DOMAIN.has(domain)) {
    return domainStateColorProperties(domain, stateObj, scope, active, state);
  }

  return undefined;
};

export const stateColorBrightness = (stateObj: HassEntity): string => {
  if (
    stateObj.attributes.brightness &&
    computeDomain(stateObj.entity_id) !== 'plant'
  ) {
    // lowest brightness will be around 50% (that's pretty dark)
    const brightness = stateObj.attributes.brightness;
    return `brightness(${(brightness + 245) / 5}%)`;
  }
  return '';
};
